import { Link, useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import { jwtDecode } from 'jwt-decode';
import alert from 'sweetalert2';
import "./TopMenu.css";

const TopMenu = () => {
    const [role, setRole] = useState(null);
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const [nickname, setNickname] = useState("");
    const navigate = useNavigate();

    const fetchRoleFromToken = () => {
        const token = localStorage.getItem("authToken");
        if (token) {
            try {
                const payload = jwtDecode(token);
                if (payload.exp * 1000 > Date.now()) {
                    //    
                    setRole(parseInt(payload.role, 10));
                    setNickname(payload.mem_nickname);
                } else {
                    //   
                    localStorage.removeItem("authToken");
                    setRole(null);
                }
            } catch (e) {
                console.error("  :", e);
                setRole(null);
            }
        } else {
            setRole(null);
        }
    };

    useEffect(() => {
        fetchRoleFromToken();

        const handleLoginEvent = () => {
            fetchRoleFromToken();
        };

        window.addEventListener("login", handleLoginEvent);
        return () => {
            window.removeEventListener("login", handleLoginEvent);
        };
    }, []);

    const handleLogout = async () => {
        localStorage.removeItem("authToken");
        setRole(null);
        setNickname("");
        await alert.fire({
            icon: "success",
            title: "",
            text: "  ~",
        });
        navigate("/");
    };

    const toggleMenu = () => {
        setIsMenuOpen(!isMenuOpen);
    };

    return (
        <nav className="top_nav">
            <div className="hamburger" onClick={toggleMenu}>
                <div></div>
                <div></div>
                <div></div>
            </div>
            <div className="top_first">
                <div className="top_menu">
                    <ul>
                        <li>
                            <Link className="logo" to={"/"}>
                                <img src="/images/logo.png" alt="" className="logo_image" />
                                
                            </Link>
                        </li>
                        <li className="search_container">
                            <input type="text" placeholder=" " className="search_box" />
                            <img src="/images/search.png" alt="" className="search_icon" />
                        </li>
                    </ul>
                </div>
                <div className="top_menu_btn">
                    {!role && (
                        <>
                            <Link to="/sign_up">
                                <button className="menu_btn"></button>
                            </Link>
                            <Link to="/login">
                                <button className="menu_btn"></button>
                            </Link>
                        </>
                    )}
                    {role === 1 && (
                        <>
                            <span className="user_auth">{nickname}</span>
                            <Link to="/mypage">
                                <button className="menu_btn"></button>
                            </Link>
                            <Link to="/userinfo">
                                <button className="menu_btn"></button>
                            </Link>
                            <button className="menu_btn" onClick={handleLogout}>
                                
                            </button>
                        </>
                    )}
                    {role === 2 && (
                        <>
                            <span className="user_auth">{nickname}</span>
                            <Link to="/admin_userlist">
                                <button className="menu_btn"></button>
                            </Link>
                            <Link to="/userinfo">
                                <button className="menu_btn"></button>
                            </Link>
                            <button className="menu_btn" onClick={handleLogout}>
                                
                            </button>
                        </>
                    )}
                </div>
            </div>
            <div className={`second_menu ${isMenuOpen ? "show" : "hide"}`}>
                <ul>
                    <li>
                        <Link to={"/about"}> </Link>
                    </li>
                    <li>
                        <Link to={"/find_store"}> </Link>
                    </li>
                    <li>
                        <Link to={"/ranking"}></Link>
                    </li>
                    <li>
                        <Link to={"/pick_crew"}> </Link>
                    </li>
                </ul>
            </div>
        </nav>
    );
};

export default TopMenu;